
.navbar{
    position: fixed;
    top:0;
    width: 100%;
    height: 20px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: black;
    color: white;
    z-index: 10;
   
}

.logo{
position: fixed;
left: 20px;
width: 110px;
height: 30px;
margin-top: -6px;
}

.ultsm{
    margin-left: 750px;
    height: 33px;
    width: 230px;
    margin-top: -6px;
    margin-right: 35px;
    border: 0;
    border-radius: 2px;
    box-sizing: border-box;
    font-size: 13px;

}

.buttonjoin{
    width: 120px;
    height: 33px;
    background-color:  #e50914;
    color: white;
    cursor: pointer;
    font-weight: 400;
    text-align: center;
    border: 0;
    box-sizing: border-box;
    border-radius: 2px;
    vertical-align: middle;
    text-decoration: none;
    letter-spacing: .1px;
    margin-left: -40px;
    margin-top: -6px;
    margin-right: -20px;
   
}

.buttonsign{
    color: white;
    font-weight: 400;
    border: 1px solid #888;
    border-radius: 2px;
    width: 95px;
    height: 33px;
    background-color: transparent;
    margin-top: -6px;
    margin-right: 40px;

}